<template>
  <main class="main-content">
    <header>
        <h2 v-once> {{name}}  </h2>
        <small> created by {{creator}}  </small>        
    </header>

    <section>
      <div class="edges-container">
        <div>
          <h3> {{crrQuestion.question}} </h3>
          <p class="question-information"> question #{{qindex + 1}} for {{crrQuestion.points}} Points </p>
        </div>
        <div>
          <p> Points </p>
          <p> {{crrPoints}} / {{totalPoints}}  </p>
        </div>        
      </div>



      <InputContainer
        v-model="answer"
        inputId="answer"
        text=""
        v-if="answerStatus ===  'not-send'"
      ></InputContainer>
      <p v-else
        :class='[ answerStatus === "correct" ? "correct-answer" : "wrong-answer" ]'
      >
       {{answerQuestionMsg}}
      </p>

      <button v-if="answerStatus ==='not-send'" @click="checkAnswer" class="custom-button size-2" > Answer </button>
      <button 
        v-else-if="qindex !== questions.length - 1" 
        @click="nextQuestion" 
        class="custom-button size-2" 
      > 
      Next Question 
      </button>
      <button 
        v-else 
        @click="$emit('quiz-finished',crrPoints)" 
        class="custom-button size-2" 
      > 
        Finish Quiz
      </button>
    </section>



  </main>
</template>

<script>
import InputContainer from "./InputContainer"

export default {
  props: ["questions","name","creator"],
  components : {
    InputContainer
  },
  data()
  {
    return {
      qindex : 0,
      answer : "",
      crrPoints : 0,
      answerStatus : "not-send",

    }
  },
  methods: {
    checkAnswer()
    {
      if( this.answer.trim().toLowerCase() == this.crrQuestion.answer.trim().toLowerCase()  )
      {
        this.crrPoints += this.crrQuestion.points;
        this.answerStatus = "correct";
        this.answer = "";
      }
      else
      {
        this.answerStatus = "wrong";
      }

    },
    nextQuestion()
    {
      if( this.qindex < this.questions.length - 1 )
      {
        this.qindex++;
        this.answerStatus = "not-send"
      }
    }
  },
  computed: {
    crrQuestion () {
      return this.questions[this.qindex];
    },
    totalPoints()
    {
      return this.questions.reduce( (acc,x)=> x.points + acc ,  0)
    },
    answerQuestionMsg()
    {
      return ( this.answerStatus === "correct" ? "Your Anwser was Right!" : "YIKES!, Wrong Anwser" );
    }
  }
}
</script>

<style scoped>

  .main-content header
  {
    display: flex;
    align-items:baseline;
    border-bottom: solid 1px black ;
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
  }

  header small
  {
    margin-left:0.2rem;
    font-style: italic;
    font-weight: bold;
    font-size: 0.7rem;
    color: #336
  }

  .question-information 
  {
    color: grey;
    font-size: 0.8rem;
    font-weight: bold;
  }

  .main-content h3
  {
    margin-left: 0;
  }

  .main-content section 
  {
    padding: 0.5rem 0.8rem 0.5rem 0.8rem;
  }

  .main-content .custom-button
  {
    padding: 0.1rem 0.4rem;
    font-size: 1rem;
    margin: 0.3rem 0;
  }

  .edges-container
  {
    display: flex;
    justify-content: space-between;
    flex-direction: row ;
  }

  .correct-answer
  {
    color: green;
    margin: 0.4rem 0 ;
    font-size: 1.4rem;
    font-weight: lighter;
  }

  .wrong-answer
  {
    color: red;
    font-size: 1.4rem;
    margin: 0.4rem 0 ;
    font-weight: lighter;

  }


</style>