<template>
  <main class="main-content">
    <h2> Quiz Finished! </h2>
    <section class="quiz-results">
      <p> <span>  </span>{{name}} </p>
      <p> Quiz Made By {{creator}} </p>
      <p> {{points}} out of {{totalPoints}} Points </p>
      <button 
        class="custom-button size-1"
        @click="$emit('finish')"
      > Go back to the start </button>
    </section>
  </main>
</template>

<script>
export default {
  props: ["name","creator","questions","points"],
  computed: {
    totalPoints() {
      return this.questions.reduce( (acc,x)=> x.points + acc ,  0)
    }
  }
}
</script>

<style scoped>
  h2 
  {
    color: lightblue;
    text-align: center;
    font-size: 1.8rem;
  }

  .main-content
  {
    padding: 1rem;
  }

  .quiz-results
  {
    width: 90%;
    margin: 0 auto;
  }

  .quiz-results .custom-button
  {
    display: block;
    margin: 0 auto;
  }

  .quiz-results p
  {
    margin: 0.5rem 0;
    font-size: 1.2rem;
  }



</style>