<template>
    <main class="main-content">
        <header>
            <h2> Quiz Settings </h2>
            <p> Hello! , let's begin setting the quiz </p>
        </header>


        <section>
          <InputContainer
            text="Name of the quiz"
            inputId="name"
            v-model="name"
            :error="nameError"
          ></InputContainer>


          <InputContainer
            text="Creator of quiz"
            inputId="creator"
            v-model="creator"
            :error="creatorError"
          ></InputContainer>

          <InputContainer
            text="Description of quiz"
            inputId="description"
            v-model="description"
            type="textarea"
            :error="descriptionError"
          ></InputContainer>
          <button @click="createQuiz" class="custom-button size-1"> Continue </button>
        </section>
    </main>
</template>


<script>
import InputContainer from "./InputContainer"

export default {
    components:{
      InputContainer
    },
    data() {
        return {
            name : "",
            creator : "",
            description : "",
            errorFlag : false,
        }
    },
    methods : {
        createQuiz() {
            this.errorFlag = true;
            if(  this.nameError === null && this.creatorError === null  && this.descriptionError === null  )
            {
                this.errorFlag = false;

                let data = {
                  'creator' : this.creator  , 
                  'name'    : this.name ,
                  'description': this.description
                  }
                this.$emit("quiz-created",data);  
            }
        }
    },
    computed: {
      nameError()
      {
        if(this.errorFlag && this.name.trim() == "")
          return "dont cant leave name field empty"
        else
          return null;
      },
      creatorError()
      {
        if(this.errorFlag && this.creator.trim() == "")
          return "dont leave creator field empty"
        else
          return null;
      },
      descriptionError()
      {
        if(this.errorFlag && this.description.trim() == "")
          return "dont leave description field empty"
        else
          return null;
      }
    }


}
</script>


<style scoped>

.custom-button
{
  margin: 0.5rem 0;
}

</style>