<template>
 <main class="main-content">
    <header>
        <h2> Question Creation for <span v-once> {{name}} </span> </h2>
        <p> now <span v-once> {{creator}} </span> , it's time to start adding the questions of the quiz</p>        
    </header>

    <section>
      <h3>Create New Question</h3>

      <InputContainer
        name="question"
        v-model="question"
        :error="questionError" 
      ></InputContainer>

      <InputContainer
        v-model="answer"
        name="answer"
        :error="answerError" 
      ></InputContainer>

      <InputContainer
        v-model="points"
        type="number"
        name="points"
        :error="pointsError" 
      ></InputContainer>

      <button  @click="addQuestion" class="custom-button size-2"> Add Question </button>
      
      
    </section>



    <section>
      <h3> Question List </h3>
      <ul v-if="questions.length > 0">
          <li v-for="(q,index) of questions" :key="index">
            <QuestionThumbnail :question="q" ></QuestionThumbnail>
          </li>
      </ul>
      <p  id="no-question-msg" v-if=" questions.length < 3" >  Add at three  questions to test a quiz  </p>
      <button @click="makeQuiz" 
        class="custom-button size-2" 
        v-if="questions.length > 2"
      > 
        Test Quiz
      </button>
    </section>
</main>
</template>


<script>

import InputContainer from './InputContainer'
import QuestionThumbnail from './QuestionThumbnail'

export default {
  data() {
      return {
          errorFlag : false,
          question  : '' ,
          answer    : '',
          points    : 0,
          questions : [],
          errors    : {
            question: null , 
            answer  : null ,
            points  : null 
          }
      }
  },
  components: {
    InputContainer,
    QuestionThumbnail
  },
  props :  {  
      creator : String,
      name : String
  },
  methods: {
      done() {

      },
      addQuestion(){
        this.errorFlag = true


        if( this.questionError === null && this.answerError === null && this.pointsError === null )
        {

          this.questions.push({
            question: this.question,
            answer: this.answer,
            points: parseFloat(this.points)
          })

          this.question = "";
          this.answer   = "";
          this.points   = "";
          this.errorFlag = false;
        }
      },
      makeQuiz()
      {
        this.$emit("quiz-made",this.questions);
      }
  },

  computed: {
    answerError() { 
      if(this.errorFlag && this.answer.trim() === '')
        return "can leave anwser field empty";
      else 
        return  null;
    },
    questionError() {
      if(this.errorFlag && this.question.trim() === '')
        return "can leave question field empty";
      else 
        return null;
    },
    pointsError() {
      if(this.errorFlag && this.points < 1)
        return "points must be greater than 0";
      else 
        return null;
    }
  }
}


</script>

<style scoped>

#no-question-msg
{
  color: grey;
  font-size: 0.8rem;
  text-align: center;
  font-weight: bold;
  margin: 1rem 0 0.5rem 0 ;
}




</style>