<template>
  <div id="app">
    <header id="main-header">
      <h1>Quiz Generator <small>made by jmml</small></h1>
    </header>

    <CreateQuiz v-if="pageState === 'quiz-creation'"  
      @quiz-created="onQuizCreated"
    ></CreateQuiz>
    <AddQuestion v-else-if="pageState === 'question-creation'"  
      :creator="creator" 
      :name="name"
      @quiz-made="onQuestionAdded"
    ></AddQuestion>
    <QuizTry v-else-if="pageState === 'question-solving'"
      :creator="creator" 
      :name="name"
      :questions="questions"
      @quiz-finished="onQuizFinished"
    >
    </QuizTry>
    <EndQuiz 
      v-else
      :creator="creator" 
      :name="name"
      :questions="questions"
      :points="points"
      @finish="restart"
    >
    </EndQuiz>
  </div>
</template>

<script>

import CreateQuiz from "./components/CreateQuiz"
import AddQuestion from "./components/AddQuestions"
import QuizTry from "./components/QuizTry"
import EndQuiz from "./components/EndQuiz"

export default {
  name: 'App',
  components: {
    CreateQuiz,
    AddQuestion,
    QuizTry,
    EndQuiz 
  },
  data() {
    return {
      crrState : 0,
      pageStates : [
        "quiz-creation",
        "question-creation",
        "question-solving",
        "quiz-end" 
      ],
      creator : "",
      name : "" ,
      description : '', 
      points : 0,
      questions : []
    }
  },
  methods: {
    onQuizCreated({creator,name,description})
    {
      this.name = name;
      this.creator = creator;
      this.description = description;
      this.crrState = 1;
    },
    onQuestionAdded(questions)
    {
      this.questions = questions;
      this.crrState = 2;
    },
    onQuizFinished(points)
    {
      this.points = points;
      this.crrState++;
    },
    next()
    {
      this.crrState++;
      if(this.crrState == 4)
        this.crrState = 0;
    },
    restart()
    {
      this.crrState = 0;
      this.questions = null;
      this.creator   = "";
      this.points = 0;
      this.name     = "";
    }
  },
  computed : {
    pageState() {
      return this.pageStates[this.crrState];
    } 
  }

  
}
</script>

