<template>
    <div class="input-container">
        <label :for="inputIdFinal"> 
          {{textFinal}}
          <small v-show="error!= null"
            class="error-warning" 
          > 
            {{error}}
          </small> 
        </label>

        <input 
          v-if="type=== 'text' || type === 'number' |  type === 'password'"
          :value="value"
          :class="{'field-error' : error != null }"
          :type="type" 
          :id="inputIdFinal"
          @input="$emit('input',$event.target.value)"
        >
        <textarea
          v-else
          :value="value"
          :class="{'field-error' : error != null }"
          @input="$emit('input',$event.target.value)"
          :id="inputIdFinal" cols="30" rows="10"
        ></textarea>
    </div>
</template>


<script>
export default {
    props : {
        type   :  { type : String  , default : "text"} ,
        value   : { required : true}  , 
        text    : { type : String , default : null}     ,
        name    : String ,
        error: { type : String , default : null} ,
        inputId: { type : String , default : null }
    } ,
    data(){
        return {
        }
    },
    computed : {
      inputIdFinal()
      {
        if(this.inputId)
          return this.inputId.toLowerCase();
        if(this.name)
          return this.name.toLowerCase() + "-input";
        return ""
      },
      textFinal()
      {
        if(this.text)
          return this.text;
        if(this.name)
          return this.name[0].toUpperCase() +  this.name.slice(1);
        return "";
      },

    }


}
</script>


<style scoped>
  .input-container
  {
      display: flex;
      flex-direction: column;
      margin: 0.4rem 0;
  }

  .input-container label
  {
      margin-bottom: 0.2rem;
  }
</style>